import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/components/Home'
import About from '@/components/About'

Vue.use(Router)

const routes = [
  {
    path: '/Home',
    component: Home
  },
  {
    path: '/About',
    component: About
  }
]

var router = new Router({
  routes
})

export default router



